<template>
    <div>
        <div class="box-container">
            <div class="box-btn">
                <h4>平台注册用户</h4>
            </div>
            <!-- 添加用户 -->
            <div class="box-btn">
                <el-button type="success" @click="addUser">添加用户</el-button>
                <el-input v-model="search.username" placeholder="请输入用户账号"></el-input>
                <el-button v-if="isfind" type="warning" @click="findUser">查询用户</el-button>
                <el-button v-else type="danger" @click="cancelFind">取消</el-button>                  
            </div>
            <!-- 数据部分 -->
            <el-table
                v-loading="loading"
                :data="userList"
                style="width: 100%;margin-bottom: 20px;"
                >
                <el-table-column type="expand" label="密码">
                    <template slot-scope="props">
                        <el-form label-position="left" inline class="demo-table-expand">
                            <el-form-item label="账号密码：">
                                <span>{{ props.row.password }}</span>
                            </el-form-item>
                        </el-form>
                    </template>
                </el-table-column>
                <el-table-column prop="username" align="center" label="用户账号"></el-table-column>
                <el-table-column prop="realName" align="center" label="用户姓名"></el-table-column>
                <el-table-column prop="mail" align="center" label="邮箱"></el-table-column>
                <el-table-column prop="phone" align="center" label="手机号"></el-table-column>
                <el-table-column align="center" label="用户身份">
                    <template slot-scope="{row}">
                        <el-tag :type="row.role | roleType">{{ row.role | roleVal }}</el-tag>
                    </template>
                </el-table-column>
                <el-table-column align="center" label="登录状态">
                    <template slot-scope="{row}">
                        <el-tag :type="row.loginStatus | statusType">{{ row.loginStatus | statusVal }}</el-tag>
                    </template>
                </el-table-column>
                <el-table-column label="操作" align="center">
                    <template slot-scope="scope">
                        <el-button size="mini" type="primary" @click="onEdit(scope.row)">编辑</el-button>
                        <el-button size="mini" type="danger" @click="onDel(scope.row)">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>

        <!-- 添加用户 -->
        <AddUser titleText="添加用户"
            :oneData="form"
            :dialogFormVisible="dialog"
            @setData="addMyData"
            @setDialog="getDialog"
            ></AddUser>
        <!-- 编辑用户信息 -->
        <AddUser titleText="编辑用户"
            :isEdit="true"
            :oneData="oneData"
            :dialogFormVisible="edialog"
            @setData="editMyData"
            @setDialog="getDialog"
            ></AddUser>
        <!-- 底部导航页 -->
        <div class="bottom-page">
            <Pagination
                :totalMsg="query.total"
                :pageSize="query.pageSize"
                :pageIndex="query.pageIndex"
                @setSize="getSize"
                @setIndex="getIndex"
            >
            </Pagination>
        </div>
    </div>
</template>

<script>
import Pagination from '@/components/Pagination.vue';
import AddUser from './components/AddUser';
import { checkEmail,checkPhone } from '@/assets/permission.js'

export default {
    components:{
        Pagination,
        AddUser,
    },
    filters:{
        statusType(type){
            const mapType={
                true:'success',
                false:'info'
            }
            return mapType[type];
        },
        statusVal(val){
            const mapVal={
                true:'允许登录',
                false:'禁止登录'
            }
            return mapVal[val];
        },
        roleType(type){
            const mapType={
                'user':'',
                'admin':'success',
                'gov':'danger',
                'langChao':''
            }
            return mapType[type];
        },
        roleVal(val){
            const mapVal={
                'user':'平台用户',
                'admin':'平台管理员',
                'gov':'政府管理员',
                'langChao':'浪潮管理员'
            }
            return mapVal[val];
        }
    },
    data(){
        return{
            isfind:true,
            loading:false,
            dialog:false,
            edialog:false,
            username:'',
            userList:[],
            oneData:{},
            query:{
                total:null,
                pageSize:5,
                pageIndex:1
            },
            form:{
                username:'',
                password:'',
                realName:'',
                phone:'',
                mail:'',
                loginStatus:'',
                role:''
            },
            search:{
                username:''
            }
        }
    },
    methods:{
        addUser(){//添加用户
            this.dialog = true;
        },
        addMyData(data){//添加用户
            let that =this;
            let mailR=checkEmail(data.mail),
                phoneR=checkPhone(data.phone),
                pwdR = data.password.length;
            if(pwdR <6 || pwdR>16){
                return that.$message({
                    message:'密码长度在6到16之间',
                    type:'error'
                })
            }
            
            if(!phoneR){
                return that.$message({
                    message:'手机格式错误',
                    type:'error'
                })
            }
            if(!mailR){
                return that.$message({
                    message:'邮箱格式错误',
                    type:'error'
                })
            }
            this.$api.admuser({
                username:data.username,
                password:data.password,
                realName:data.realName,
                mail:data.mail,
                phone:data.phone,
                loginStatus: data.loginStatus,
                role: data.role
            }).then(res=>{
                if(res.err_code === 100){
                    that.$message({
                        message:'添加用户成功',
                        type:'success'
                    })
                    that.dialog = false;
                    that.getUserList();
                }else{
                    that.$message({
                        message:'添加用户失败',
                        type:'error'
                    })
                }
            })
        },
        getDialog(data){
            this.dialog = data;
            this.edialog = data;
            this.form.username='';
            this.form.realName='';
            this.form.password='';
            this.form.mail='';
            this.form.phone='';
            this.form.loginStatus = '';
            this.form.role = '';
        },
        onEdit(info){
            this.edialog = true;
            this.oneData = info;
        },
        editMyData(info){//编辑
            let that = this;
            that.$api.putadmuser({
                id:info.id,
                loginStatus:info.loginStatus,
                mail: info.mail,
                password: info.password,
                phone: info.phone,
                role: info.role,
                username: info.username,
                realName: info.realName
            }).then(res=>{
                if(res.err_code === 100){
                    that.$message({
                        message:'用户更新成功',
                        type:'success'
                    })
                    that.edialog = false;
                    that.getUserList();
                }else{
                    that.$message({
                        message:'用户更新删除',
                        type:'erroe'
                    })
                }
            })
        },
        onDel(info){//删除用户
            let that = this;
            this.$confirm('确定删除吗?','提示',{
                type:'warning'
            }).then(()=>{
                that.$api.deladmuser(`${info.id}`).then(res=>{
                    if(res.err_code === 100){
                        that.$message({
                            message:'删除成功',
                            type:'success'
                        })
                        that.getUserList();
                    }else{
                        that.$message({
                            message:'删除失败',
                            type:'error'
                        })
                    }
                })
            }).catch(()=>{}) 
        },
        findUser(){//查询信息
            if(this.search.username ==""){
                return this.$message({
                    message:'所填不能为空',
                    type:'error'
                })
            }
            this.isfind = false;
            this.username = this.search.username;
            this.getUserList();
        },
        cancelFind(){//取消查询
            this.isfind = true;
            this.search.username = "";
            this.getUserList();
        },
        getUserList(){//获取用户信息
            let that =this;
            that.loading = true;
            this.$api.admuserlist({
                page_num: that.query.pageIndex,
                page_size: that.query.pageSize,
                username: that.search.username
            }).then(res=>{
                if(res.err_code === 100){
                    let data = res.data.users.content;
                    that.userList = data;
                    that.query.total = data.length;
                    that.loading = false;
                }
            })
        },
        getSize(size){//底部导航
            this.query.pageSize = size;
            this.getUserList();
        },
        getIndex(index){
            this.query.pageIndex = index;
            this.getUserList();
        }
    },
    created(){
        this.getUserList();
    }
}
</script>

<style lang="scss">
    .box-btn{
        margin: 10px 0px;
        display: flex;
        .el-input{
            margin: 0 10px;
            width: 250px;
        }
    }
    .bottom-page{
        background: #fff;
        display: flex;
        justify-content: center;
    }
</style>