<template>
    <div>
        <el-dialog :title="titleText"
            @close="closeDialog"
            :visible.sync="dialogFormVisible_">
            <el-form :model="oneData">
                <el-form-item label="用户账号" :label-width="formLabelWidth">
                    <span v-if="isEdit">{{ oneData.username}}</span>
                    <el-input v-else v-model="oneData.username" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="用户姓名" :label-width="formLabelWidth">
                    <el-input v-model="oneData.realName" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="用户密码" :label-width="formLabelWidth">
                    <el-input v-model="oneData.password" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="大陆手机号" :label-width="formLabelWidth">
                    <el-input v-model="oneData.phone" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="个人邮箱" :label-width="formLabelWidth">
                    <el-input v-model="oneData.mail" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="角色身份" :label-width="formLabelWidth">
                    <el-select v-model="oneData.role" placeholder="请选择角色身份">
                        <el-option label="平台管理员" value="admin"></el-option>
                        <el-option label="平台用户" value="user"></el-option>
                        <el-option label="政府管理员" value="gov"></el-option>
                        <el-option label="浪潮管理员" value="langChao"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="登录状态" :label-width="formLabelWidth">
                    <el-select v-model="oneData.loginStatus" placeholder="请选择登录状态">
                        <el-option label="允许登录" value="true"></el-option>
                        <el-option label="禁止登录" value="false"></el-option>
                    </el-select>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button  @click="cancelDialog">取 消</el-button>
                <el-button type="primary"  @click="addData('oneData')">添 加</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
export default {
    props:{
        isEdit:{ type:Boolean,default:false},
        titleText:{type:String},
        oneData:{type:Object,default:null},
        dialogFormVisible:{ type:Boolean,default:false},
        formLabelWidth:{ type:String,default:'150px'}
    },
    computed:{
        dialogFormVisible_:{
            get(){
                return this.dialogFormVisible
            },
            set(val){
                this.$emit('setDialog',val);
            }
        }
    },
    methods:{
        closeDialog(){
            this.dialogFormVisible_ = false;
        },
        cancelDialog(){
            this.dialogFormVisible_ = false;
        },
        addData(){
            let that =this;
            if(!that.oneData.mail || !that.oneData.username ||
                !that.oneData.realName || !that.oneData.password ||
                !that.oneData.loginStatus || !that.oneData.role){
                return that.$message({
                    message:'所填数据不能为空',
                    type:'error'
                })
            }
            this.$emit('setData',this.oneData);
        }
    }
}
</script>

